import React from 'react';
import { Steps } from 'antd';


interface ItemType {
    title: string;
    description: string;
}

interface Props {
    current: number,
    items: ItemType[]
}

const description = 'This is a description.';
const App: React.FC = (props: Props) => (
    <Steps
        current={1}
        items={[
            {
                title: 'Finished',
                description,
            },
            {
                title: 'In Progress',
                description,
                subTitle: 'Left 00:00:08',
            },
            {
                title: 'Waiting',
                description,
            },
        ]}
    />
);

export default App;